<template>
  <div>
    <h2 class="page-title">{{ "ترتيب مجموع امتحانات الطلبة" | translate }}</h2>

    <!---------------------------------------->
    <div class="contaner _inputs1">
      <!-- <h3 class="subtitle">Filter</h3> -->
      <div class="row justify-content-center">
        <div class="col-md-4">
          <select v-model="filter.stage" @change="doFilter" class="form-control">
            <option
              v-for="stage in $store.getters.stages"
              :key="stage.value"
              :value="stage.value"
            >
              {{ stage.text }}
            </option>
          </select>
        </div>
        <div class="col-md-4">
          <select v-model="filter.unit" class="form-control">
            <option v-for="section in units" :key="section._id" :value="section.number">
              {{ section.name }}
            </option>
            <option :value="null">All</option>
          </select>
        </div>
        <div class="col-md-4">
          <!------------------------------------------>
          <select v-model="filter.examId" class="form-control">
            <option v-for="exam in exams" :key="exam._id" :value="exam._id">
              {{ exam.name }}
            </option>
            <option :value="null">All</option>
          </select>
        </div>
      </div>
      <div class="row justify-content-center">
        <div class="col-md-4">
          <button @click="doFilter" class="btn btn-success" :disabled="!filter.unit">
            Filter
          </button>
        </div>
      </div>
    </div>
    <br />
    <hr />
    <!--------------------------------------------------------------------------->
    <h3
      style="font-weight: bolder; color: #fff; background-color: #537175"
      class="text-center"
    >
      {{ "ترتيب الطلبة" | translate }}
    </h3>
    <div v-if="degrees.length == 0" class="center">
      <h3 class="text-center">{{ "لا يوجد طلبة" | translate }}</h3>
    </div>
    <div style="height: 700px; overflow-y: scroll" class="table-responsive">
      <!--Table-->
      <table class="table">
        <!--Table head-->
        <thead>
          <tr>
            <th class="th-sm">الترتيب</th>
            <th class="th-sm">التقدم المحرز</th>
          </tr>
        </thead>
        <!--Table head-->

        <!--Table body-->
        <tbody style="background-color: rgba(91, 103, 108, 0.17)">
          <tr style="border: 1px solid #444" v-for="(grade, g) in degrees" :key="g">
            <td>
              <div class="padge">{{ g + 1 }}</div>
            </td>
            <td style="width: 100%">
              <div class="student text-center">{{ grade._id }}</div>
              <div class="progress mt-2">
                <span>({{ grade.totalDegree }} / {{ fullDegree }})</span>
                <div
                  class="progressbar bg-dark"
                  :style="{ width: getPercetage(grade.totalDegree) + '%' }"
                >
                  {{ getPercetage(grade.totalDegree) | round }}%
                </div>
              </div>
            </td>
          </tr>

          <loading v-if="loading" class="mx-auto" />
        </tbody>
        <!--Table body-->
      </table>
      <!--Table-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ProgressBar from "../components/progress-bar.vue";
export default {
  components: {
    ProgressBar,
  },
  props: ["side", "type"],
  created() {
    // this.doFilter();
    axios.get("/getExamsNamesAndIds").then((res) => (this.allExams = res.data.exams));
  },
  data() {
    return {
      unit: null,
      allExams: [],
      degrees: [],
      fullDegree: 0,
      loading: false,
      filter: {
        stage: "6",
        unit: null,
        examId: null,
        isExam: false,
      },
    };
  },
  computed: {
    units() {
      const units = this.$store.getters.sections.filter(
        (sec) => sec.stage == this.filter.stage
      );
      return units.map((unit) => ({ ...unit, number: unit.number.toString() }));
    },
    exams() {
      const exams = this.allExams.filter((ex) => ex.unit == this.filter.unit);
      return exams;
    },
  },
  methods: {
    async doFilter() {
      this.degrees = [];
      const stage = this.side === "user" ? this.$store.getters.stage : this.filter.stage;
      this.loading = true;
      const res1 = await axios.post(`/getUnitExamsFullDegree`, this.filter);
      this.fullDegree = res1.data.fullDegree;
      // const res2 = await axios.get(`/getStudentsByStageAndUnit/${stage}/${this.unit}`);
      // const students = res2.data.students;
      axios.post(`/getStudentTotalDegree`, this.filter).then((res) => {
        if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
        this.degrees = res.data.degrees;
      });
      // this.degrees = res3.data.degrees.map((total) => {
      //   return {
      //     degree: total.totalDegree,
      //     username: students.find((stud) => stud._id == total._id)?.fullname || total._id,
      //   };
      // });
      this.loading = false;
    },
    getPercetage(degree) {
      return Math.round((degree / this.fullDegree) * 100);
    },
  },
};
</script>

